import http from '../util/http';

const PROTOCOL = `${location.protocol}//`;
const HOST = {
    TOPICM_CHE168COM: `${PROTOCOL}topicm.che168.com`
};

//接口wiki :  http://wiki.corpautohome.com/pages/viewpage.action?pageId=169449096
const API_URL = {
    GET_PROD_LIST: `${HOST.TOPICM_CHE168COM}/handler/blindbox/getprodlist.ashx`,   //盲盒-兑换商品列表
    POST_SAVE_ADDRESS: `${HOST.TOPICM_CHE168COM}/handler/blindbox/saveaddress.ashx`,   //盲盒-保存邮寄地址
    POST_EXCHANGE_PRIZE: `${HOST.TOPICM_CHE168COM}/handler/blindbox/exchangeprize.ashx`,   //盲盒-车标兑换商品
    GET_LUCKY_DRAW: `${HOST.TOPICM_CHE168COM}/handler/blindbox/luckydraw.ashx`,   //盲盒-盲盒抽车标
    POST_INSERT_BLINDBOX: `${HOST.TOPICM_CHE168COM}/handler/blindbox/insertblindboxcarlogo.ashx`,   //盲盒-增加抽盲盒机会
    GET_MUCAR_LOGO_LIST: `${HOST.TOPICM_CHE168COM}/handler/blindbox/getmycarlogolist.ashx`,   //盲盒-获取我的车标
    GET_BLIND_BOX_NUM: `${HOST.TOPICM_CHE168COM}/handler/blindbox/getblindboxnum.ashx`,   //盲盒-获取抽奖机会次数
    GET_TASK_STATUS: `${HOST.TOPICM_CHE168COM}/handler/blindbox/gettaskstatus.ashx`,  //获取任务状态
    GET_MY_PRIZE_LIST:`${HOST.TOPICM_CHE168COM}/handler/blindbox/getmyprizelist.ashx`, // 我的奖品
}

const API = {

    //我的奖品
    GET_MY_PRIZE_LIST: function (params = {}) {
        return http.getJsonp(API_URL.GET_MY_PRIZE_LIST, params);
    },

    //获取任务状态
    GET_TASK_STATUS: function (params = {}) {
        return http.getJsonp(API_URL.GET_TASK_STATUS, params);
    },

    //盲盒-兑换商品列表
    GET_PROD_LIST: function (params = {}) {
        return http.getJsonp(API_URL.GET_PROD_LIST, params);
    },

    //盲盒-保存邮寄地址
    POST_SAVE_ADDRESS: function (params = {}) {
        return http.post(API_URL.POST_SAVE_ADDRESS, params);
    },

    //盲盒-车标兑换商品
    POST_EXCHANGE_PRIZE: function (params = {}) {
        return http.post(API_URL.POST_EXCHANGE_PRIZE, params);
    },

    //盲盒-盲盒抽车标
    GET_LUCKY_DRAW: function (params = {}) {
        return http.getJsonp(API_URL.GET_LUCKY_DRAW, params);
    },

    //盲盒-增加抽盲盒机会
    POST_INSERT_BLINDBOX: function (params = {}) {
        return http.post(API_URL.POST_INSERT_BLINDBOX, params);
    },

    //盲盒-获取我的车标
    GET_MUCAR_LOGO_LIST: function (params = {}) {
        return http.getJsonp(API_URL.GET_MUCAR_LOGO_LIST, params);
    },

    //盲盒-获取抽奖机会次数
    GET_BLIND_BOX_NUM: function (params = {}) {
        return http.getJsonp(API_URL.GET_BLIND_BOX_NUM, params);
    }

}  

export { API };


