<template>
  <div class="wrapper">
    <Head :cityId="+curCity.cid" :ispc="ispc" :isLogin="isLogin"></Head>
    <PrizeExchange :cityId="+curCity.cid" :ispc="ispc"></PrizeExchange>
    <van-popup v-model="show" class="popup-self" round position="bottom">
      <div class="pop-cont">
        <div class="title"><img src="./Components/pop-title01.png" /></div>
        <div class="sub-title">
          <h5><van-icon name="checked" />兑换成功</h5>
          <p>请填写邮寄地址</p>
        </div>
        <FormNormal />
      </div>
    </van-popup>

    <van-popup
      v-model="isShowMyAutoLogos"
      class="popup-self"
      round
      position="bottom"
    >
      <div class="pop-cont">
        <!-- <div class="title"><img src="//x.autoimg.cn/2scimg/m/topic/20210226/pop-title03.png" /></div> -->
        <MyAutoLogos></MyAutoLogos>
      </div>
    </van-popup>
  </div>
</template>

<script>
import {
  isEscApp,
  isManApp,
  isWechat,
  getUrlParams,
  domAppendJs,
  cid2pid,
  getCnameFromCid,
  getPnameFromPid,
  getCookie,
} from "../../util/util";
import { loadPvJS, trackShow } from "../../util/statistics";
import "@vant/touch-emulator";
import Head from "./Components/Head";
import WinningRounds from "./Components/WinningRounds";
import PrizeExchange from "./Components/PrizeExchange";
import FormNormal from "../../components/form";
import Vue from "vue";

import { Popup } from "vant";
Vue.use(Popup);

import MyAutoLogos from "./Components/MyAutoLogos"; //我的车标

export default {
  name: "Index",
  components: {
    Head,
    WinningRounds,
    PrizeExchange,
    FormNormal,
    MyAutoLogos,
  },
  data() {
    return {
      isInApp: false,
      ispc: false,
      curCity: { cname: "全国", cid: 0 }, //当前选择城市默认为定位城市
      areaGeo: { cname: "全国", cid: 0 }, //当前选择城市默认为定位城市
      isLogin: false,
      show: false,
      isShowMyAutoLogos: true, //我的车标列表
    };
  },

  created() {
    this.initData();
    //关闭我的车标 === 需要配合添加id
    this.$eventBus.$on("topic-manghe-isshow-myautologs", (data) => {
      console.log(data);
      this.isShowMyAutoLogos = data.isShow;
      location.hash = 'center';
    });

  },
  mounted() {},
  methods: {
    initData() {
      this.isInApp = !(isEscApp() || isManApp() || isWechat());
      this.loadJS();
      this.ispc = getUrlParams()["ispc"] === "1";
      this.isUserLogin(); //判断登录状态 统一
    },

    //加载js & 定位
    loadJS() {
      domAppendJs(
        "//x.autoimg.cn/com/co.ashx?path=/2sc/m/Zepto.js,/2sc/m/CarDetail/clipboard.min.js,/2sc/m/CarList/changeAround_V2.js,/2sc/m/oneKeyLogin/login.js,/2sc/m/EvnetClickTrack.js,/bi/che/ahas_single.min.js&v=20200205",
        () => {
          require("../../util/share");

          domAppendJs(
            "//x.autoimg.cn/app/scripts/ahapp-2.0.js?v=20200205",
            () => {
              this.getAhCity();
            }
          );
          if (getUrlParams()["cid"]) {
            this.curCity.cid = getUrlParams()["cid"];
            this.curCity.cname = getCnameFromCid(this.curCity.cid) || "全国";
          }
          //获取城市
          if (!isManApp()) {
            this.getCity();
          }
          // trackShow("che168_2021_topic_newyear_cars");
          // pageeid, siteid, categoryid, subcategoryid
          // loadPvJS(124001013, 1211123, 1369, 21948);
          // this.$eventBus.$emit("2021-new-year-pageshow");
        },
        ""
      );
    },
    //获取城市
    getCity() {
      if (this.curCity.cid === 0) {
        if (isEscApp()) {
          var selectGeo = eval(
            "(" + decodeURIComponent($.getCookie("selectGeo")) + ")"
          );
          if (selectGeo !== undefined) {
            this.areaGeo = {
              cid: selectGeo.cid,
              cname: getCnameFromCid(selectGeo.cid),
              pid: selectGeo.pid,
              pname: getPnameFromPid(selectGeo.pid),
            };
            this.curCity = Object.assign({}, this.areaGeo);
          }
        } else {
          var uareaid = 110100;
          var uarea = $.getCookie("uarea");
          if (uarea != undefined && uarea != "") {
            uareaid = parseInt(uarea.split("|")[0]);
          }
          if (
            uareaid == 110000 ||
            uareaid == 500000 ||
            uareaid == 120000 ||
            uareaid == 310000
          ) {
            uareaid += 100;
          }
          if (uareaid == 0) {
            uareaid = 110100;
          }

          //如果用户选择省则取省会
          if (uareaid.toString().indexOf("0000") > -1) {
            uareaid = +uareaid + 100;
          }

          this.curCity = {
            pid: cid2pid(uareaid),
            cid: +uareaid,
            cname: getCnameFromCid(uareaid),
          };

          let carea = $.getCookie("area");
          carea = carea.split("|")[0];
          carea = this.judgeAreaID(+carea); //过滤城市数据库里没有的值
          if (carea > 0) {
            let areaNum = carea;
            areaNum = areaNum - (areaNum % 100);
            if (areaNum.toString().indexOf("0000") > -1) {
              areaNum = areaNum + 100;
            }

            this.areaGeo = {
              cid: areaNum,
              pid: cid2pid(areaNum),
              cname: getCnameFromCid(areaNum),
            };
            console.log("areaGeo : ", this.areaGeo);
          } else {
            this.curCity = Object.assign({}, this.areaGeo);
          }
          // alert($.getCookie("uarea") + " -- " + $.getCookie("area"));
          // alert(JSON.stringify(this.areaGeo));
        }
        this.$eventBus.$emit("2021-new-year-area-data", {
          areaGeo: this.areaGeo,
        });
        this.getData();
      }
    },

    //判断定位城市id
    judgeAreaID(id) {
      let ID = id.toString();
      if (ID.length !== 6) {
        return 0;
      }
      let arr = [
        "1111",
        "2222",
        "3333",
        "4444",
        "5555",
        "6666",
        "7777",
        "8888",
        "9999",
      ];
      let arr1 = [
        "000000",
        "111111",
        "222222",
        "333333",
        "444444",
        "555555",
        "666666",
        "777777",
        "888888",
        "999999",
      ];

      if (arr1.includes(ID)) {
        return 0;
      }
      if (+ID.substring(0, 2) >= 83) {
        return 0;
      }
      if (arr.includes(ID.substring(2, ID.length))) {
        if (+ID.substring(0, 2) < 83) {
          return parseInt(id / 10000) * 10000;
        } else {
          return 0;
        }
      }
      return id;
    },

    //主软获取城市1
    getAhCity() {
      if (this.curCity.cid === 0) {
        if (isManApp()) {
          AHAPP.invokeNative("getSyncCity", {
            success: (result) => {
              // console.log('getSyncCity - > ', result);
              let {
                provinceId,
                cityId,
                cityName,
                provinceName,
              } = result.result;
              this.areaGeo = {
                cid: cityId,
                cname: cityName,
                pname: provinceName,
                pid: provinceId,
              };
              this.curCity = Object.assign({}, this.areaGeo);
              this.$eventBus.$emit("2021-new-year-area-data", {
                areaGeo: this.areaGeo,
              });
              this.getData();
            },
            fail: function (result) {
              this.getData();
            },
          });
        } else {
          this.getData();
        }
      } else {
        this.getData();
      }
    },
    //用户是否登录
    isUserLogin() {
      let isLogin = false;
      if (isManApp() || isEscApp()) {
        const pcpopclub = getCookie("pcpopclub") || "";
        isLogin = pcpopclub.length > 10;
        console.log("pcpopclub", pcpopclub, isLogin);
      } else {
        const clubUserShow = getCookie("clubUserShow") || "";
        isLogin = clubUserShow.length > 10;
        console.log("clubUserShow", clubUserShow, isLogin);
      }
      let miniProgramSession = getCookie("miniProgramSession");
      if (miniProgramSession && miniProgramSession.length > 0) {
        isLogin = true;
      }
      this.isLogin = isLogin;
      console.log("isLogin - >", isLogin);
    },
    getData() {},
  },
};
</script>

<style lang="scss">
.wrapper {
  height: 100%;
  margin: 0 auto;
  max-width: 750px;
  min-height: 100vh;
  background-color: #f2f2f2;

  // background-image: linear-gradient(239deg, rgba(255,166,52,0.00) 37%, rgba(46,12,42,0.45) 65%, rgba(84,62,27,0.60) 75%, rgba(0,25,45,0.70) 85%);
  .popup-self {
    overflow-y: inherit;
    .pop-cont {
      padding-top: 50px;
      position: relative;
      .title {
        position: absolute;
        top: -8px;
        text-align: center;
        z-index: 10;
        left: 0;

        img {
          width: 100%;
        }
      }
    }
    .sub-title{
      text-align: center;
      padding-bottom: 20px;
      h5{
        font-size: 18px;
        color: #1B1B1B;
        line-height: 1;
        .van-icon-checked{
          color: #45C186;
          font-size: 19px;
          vertical-align:-3px;
          margin-right: 3px;
        }
      }
      p{
        font-size: 14px;
        color: #666666;
        line-height: 1;
        padding-top: 8px;
      }
    }
  }
}

.flex-row-center {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.flex-row-space-around {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}

.flex-colnum-center {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
</style>


