
import { domAppendJs } from './util';

//加载埋点js
function loadPvJS(pageeid, siteid, categoryid, subcategoryid, platform = 2) {
  domAppendJs('//x.autoimg.cn/che168/pv/js/pv_m_1_3.js?201611161705');
  //load完上面的js再调用发送事件
  setTimeout(() => {
    sendPV(pageeid, siteid, categoryid, subcategoryid, platform = 2);
  }, 888);
};

//必须页面统计js加载完之后才能调用
//定时器时间最好设置高于1秒，trackCustomClick方法依赖于一个trackCustomEvent,时间较短的时候回拿不到这个方法，从而被return，而无法发送请求埋点。
function trackShow(type) {
  setTimeout(() => {
    let objs = $('body').find('[s_customeventkey_type="' + type + '"]');
    if (objs.length > 0) {
      objs.each(function () {
        var s_che_json = $(this).attr('s_che_json') || '{}';
        s_che_json = eval('(' + s_che_json + ')');
        var s_che_extendsJson = $(this).attr('s_che_extendsJson') || '{}';
        s_che_extendsJson = eval('(' + s_che_extendsJson + ')');
        trackCustomClick(s_che_json, s_che_extendsJson);
        
        console.log('show发送成功');
      })
    }
  }, 1000);

  // self.find('[s_customeventkey="s_che_eventkey"]').each(function (k, v) {
  //     var s_che_json = $(this).attr('s_che_json') || '{}';
  //     s_che_json = eval('(' + s_che_json + ')');
  //     var s_che_extendsJson = $(this).attr('s_che_extendsJson') || '{}';
  //     s_che_extendsJson = eval('(' + s_che_extendsJson + ')');
  //     if (self.showKey.indexOf(s_che_json.action + '_' + s_che_extendsJson.pos + '_' + type) > -1)
  //         return;
  //     trackCustomClick(s_che_json, s_che_extendsJson);
  //     self.showKey.push(s_che_json.action + '_' + s_che_extendsJson.pos + '_' + type);
  // });
};

//发送pv
function sendPV(pageeid, siteid, categoryid, subcategoryid, platform = 2) {
  //必须挂到window 下面否则pv不带参数 坑
  window.pvTrack = new PVTrack();
  window._trackPVTrigger = true;
  window.pvTrack.object = 0;
  window.pvTrack.site = siteid;
  window.pvTrack.category = categoryid;
  window.pvTrack.subcategory = subcategoryid;
  window.pvTrack.platform = platform;
  window.pvTrack.page = pageeid;

  //这两个必须在设置pvTrack变量之后调用会自动往集团发埋点（而且是自动的）
  // domAppendJs('//x.autoimg.cn/bi/che/ahas_single.min.js?v=202001130');
  // domAppendJs('//x.autoimg.cn/bi/partner/che_cp.min.js?v=20170427');

  if ($("#ahas_single")[0]) {
    window.trackPageView(pvTrack)
  } else {
    domAppendJs('//x.autoimg.cn/bi/che/ahas_single.min.js?v=202001130', () => {
      window.trackPageView(pvTrack)
    }, 'ahas_single');
  }



  !(function (doc) {
    var _as = doc.createElement('script');
    _as.type = 'text/javascript';
    _as.async = true; _as.src = 'https://x.autoimg.cn/2sc/ev/m.js?d=a' + Math.floor((new Date()).getTime() / (1000 * 60 * 60 * 24));
    var s = doc.getElementsByTagName('script')[0];
    s.parentNode.insertBefore(_as, s);
  })(document);

  console.log('pvTrack : ', JSON.stringify(window.pvTrack));
  window.pvTrack.track();

  //百度
  var _hmt = _hmt || [];
  (function () {
    var hm = document.createElement('script');
    hm.src = 'https://hm.baidu.com/hm.js?0f2ac73eb429af8bb7f48d01f2a25a25';
    var s = document.getElementsByTagName('script')[0];
    s.parentNode.insertBefore(hm, s);
  })();
}


export { loadPvJS, trackShow };


