(function () {
    var url = location.href.toLowerCase();
    if (url.indexOf('.che168.com/dealer/') === -1 && url.indexOf('.che168.com/personal/') === -1) {
        $('body').prepend('<div id="wx_pic" style="margin:0 auto;display:none;"><img src="http://www.autoimg.cn/2scimg/m/website/20150421/300x300_che168.jpg" /></div>');
    }
})()
    /* Append File:/2sc/m/share/share.js */
    ; (function ($, document, window, undefined) {
        //useragent
        var ua = window.navigator.userAgent.toLowerCase();
        var string = String;
        if (typeof (String.prototype.contains) === "undefined") {
            String.prototype.contains = function (key) {
                if (typeof (key) === 'undefined')
                    return false;
                return String(this).indexOf(key) > -1;
            };
        }
        let isHaveFn = () => { };
        //构造函数
        function ushare(settings) {
            var self = this;
            self.settings = settings;
            if (ua.contains('che_android') || ua.contains('che_ios'))
                self.appShareInit();
            else if (ua.contains('auto_iphone') || ua.contains('auto_android'))
                self.dynamicLoadJs('//x.autoimg.cn/app/scripts/ahapp-2.0.js');
            if (settings.hasOwnProperty('target') || settings.target) {
                $(settings.target).on('click', function () {
                    self.clickShare();
                });
            }
            else
                self.share();
        };
        //原型扩展
        ushare.prototype = {
            //右上角分享
            share: function () {
                var self = this;
                if (ua.contains('auto_android') || ua.contains('auto_iphone'))
                    self.autoShare();
                else if (ua.contains('che_android') || ua.contains('che_ios'))
                    self.appShare();
                else
                    return;
            },
            //点击分享
            clickShare: function () {
                var self = this;
                if (ua.contains('auto_android') || ua.contains('auto_iphone')) {
                    self.autoClickShare();
                }
                else if (ua.contains('che_android') || ua.contains('che_ios')) {
                    self.appClickShareShowShareDialog();
                }
                else
                    return;
            },
            //二手车右上角分享
            appShare: function () {
                var self = this;
                setTimeout(function () {
                    isHaveFn('sharePage', function (can) {
                        if (can) {
                            appbridge.invoke('sharePage', self.sharePageArgs, (data) => {
                                if (data) {
                                    self.settings.callback && self.settings.callback(data);
                                }
                            });
                        }
                        else {
                            //二手车app环境初始较慢的情况下过3s重试一次
                            setTimeout(function () {
                                appbridge.invoke('sharePage', self.sharePageArgs, (data) => {
                                    if (data) {
                                        self.settings.callback && self.settings.callback(data);
                                    }
                                });
                            }, 3000);
                        }
                    });
                }, 2000);
            },
            //二手车点击分享(865以下支持)
            appClickShare: function () {
                var self = this;
                self.sharePageArgs.channel = 'all';
                let args = Object.assign({}, self.sharePageArgs);
                args.weichatfriend.minpath = '';
                args.weichatfriend.userName = '';
                // alert(JSON.stringify(args));
                isHaveFn('sharePageOnChannel', function (can) {
                    if (can) {
                        appbridge.invoke('sharePageOnChannel', args, (data) => {
                            if (data) {
                                self.settings.callback && self.settings.callback(data);
                            }
                        });
                    }
                });
            },
            //二手车点击分享(app865以上支持新增分享小程序版)
            appClickShareShowShareDialog: function () {
                var self = this;
                let args = Object.assign({}, self.sharePageArgs);
                delete args.weichatfriend;
                delete args.channel;
                // delete args.default;
                delete args.weibo;
                console.log(args);
                isHaveFn('showShareDialog', function (can) {
                    if (can) {
                        appbridge.invoke('showShareDialog', args, (data) => {
                            if (data) {
                                self.settings.callback && self.settings.callback(data);
                            }
                        });
                    } else {
                        self.appClickShare();
                    }
                });
            },
            //主软件右上角分享
            autoShare: function () {
                var self = this;
                setTimeout(function () {
                    AHAPP.invokeNative('actionbarinfo', {
                        title: self.settings.pageTitle || self.settings.title,
                        addmenulist: [],
                        stablemenulist: ['share']
                    });

                    AHAPP.setNativeShareInfo({
                        platform: self.settings.platform,
                        url: self.settings.url,
                        title: self.settings.title,
                        content: self.settings.content,
                        imgurl: self.settings.imageUrl
                    });
                }, 2000);
            },
            //主软件点击分享  http://wiki.corpautohome.com/pages/viewpage.action?pageId=80987503
            autoClickShare: function () {
                var self = this;
                let parasms = {
                    platform: self.settings.platform,
                    url: self.settings.url,
                    title: self.settings.title,
                    // options: {
                    //     wx_mini_program: {
                    //         needlogin:true,
                    //         userName: 'gh_789f6dd85253',
                    //         type: 2,  //要跳转的小程序对应的版本，选填 0：正式版本（默认） 1：测试版本 2：体验版本
                    //         path: `webview/che168/index?url=${encodeURIComponent(self.settings.url)}&sharetitle=${self.settings.title}&shareimage=${encodeURIComponent(self.settings.imageUrl)}`
                    //     }
                    // },
                    content: self.settings.content,
                    imgurl: self.settings.imageUrl,
                    success: (data) => {
                        if (data) {
                            self.settings.callback && self.settings.callback(data);
                        }
                    },
                    fail: (data) => {
                        if (data) {
                            self.settings.callback && self.settings.callback(data);
                        }
                    }
                }
                if (self.settings.ahOptions) {
                    parasms.options = self.settings.ahOptions;
                }
                // alert(JSON.stringify(parasms));
                AHAPP.invokeNative('h5share', parasms);
            },
            //app环境初始化
            appShareInit: function () {
                var self = this;
                const content = self.settings.content;
                const shareURL = self.settings.url;
                var weichatfriend = { content: content };
                let liteParams = {
                    userName: 'gh_789f6dd85253',
                    type: 0,  //要跳转的小程序对应的版本，选填 0：正式版本（默认） 1：测试版本 2：体验版本
                    minpath: `package/pages/h5/index/index?url=${encodeURIComponent(self.settings.url)}&sharetitle=${self.settings.title}&shareimage=${encodeURIComponent(self.settings.imageUrl)}`
                };
                if (self.settings.hasOwnProperty('wxxcx') && self.settings.wxxcx) {
                    var minpath = shareURL.replace('?', '&').replace(/#/g, "&");
                    weichatfriend = {
                        minpath: '/view/activity/index?url=' + minpath + '&sourcename=2scxcx',  //小程序用 path
                        title: self.settings.title,
                        content: content,
                        imageurl: self.settings.imageUrl,
                        url: shareURL
                    };
                }
                if (self.settings.hasOwnProperty('autojsxcx') && self.settings.autojsxcx) {
                    weichatfriend = Object.assign({
                        title: self.settings.title,
                        content: content,
                        imageurl: self.settings.imageUrl,
                        url: shareURL
                    }, liteParams);
                }

                self.sharePageArgs = {
                    showsharebutton: 1,
                    default: {
                        title: self.settings.title,
                        content: content,
                        imageurl: self.settings.imageUrl,
                        url: shareURL
                    },
                    weichattimeline: {
                        title: content,
                    },
                    weibo: {
                        content: content + "#二手车之家#" + shareURL
                    },
                    weichatfriend: weichatfriend,
                }
                //根据属性配置来确定是否打开小程序
                if (self.settings.hasOwnProperty('autojsxcx') && self.settings.autojsxcx) {
                    self.sharePageArgs.wx_mini_program = liteParams
                }

                if (typeof appbridge == "undefined") {
                    appbridge = {};
                    /* 绑定提供给移动端调用的js方法*/
                    appbridge.isAvailable = function () {
                        return typeof (AHJavascriptBridge) !== 'undefined';
                    };
                    appbridge.bindMethod = function (methodname, callback) {
                        if (appbridge.isAvailable()) {
                            AHJavascriptBridge.bindMethod(methodname, callback);
                        }
                    };
                    appbridge.invoke = function (methodname, args, callback) {
                        if (appbridge.isAvailable()) {
                            AHJavascriptBridge.invoke(methodname, args, callback);
                        }
                    };
                    function onBridgeReady() {
                    };
                }
                isHaveFn = function (methodName, callback) {
                    if (appbridge.isAvailable()) {
                        appbridge.invoke('getNativeBindMethods', null, function (args) {
                            var methods = args.result;
                            if (methods && typeof (methods) !== 'undefined') {
                                callback(methods.indexOf(methodName) > -1);
                            }
                        });
                    } else {
                        callback(false);
                    }

                };
            },
            //引入js
            dynamicLoadJs: function (filepath) {
                var es = document.getElementsByTagName('script');
                for (var i = 0; i < es.length; i++) {
                    if (es[i]['src'].indexOf(filepath) != -1)
                        return;
                }
                if (!filepath || filepath.length === 0) {
                    throw new Error('argument "path" is required !');
                }
                var head = document.getElementsByTagName('head')[0];
                var script = document.createElement('script');
                script.src = filepath;
                script.type = 'text/javascript';
                head.appendChild(script);
            }
        };
        window.fz = window.fz || {};
        window.fz.share = ushare;
    })(Zepto || window.Zepto, document, window);


