<template>
  <van-form class="form" @submit="onSubmit">
    <van-cell-group>
      <van-field
        class="filed-cell"
        v-model="namecell"
        label="姓名"
        placeholder="请输入收货人姓名"
        :rules="nameRules"
      />
      <p class="error" v-show="nameMassage !== ``">
        <van-icon name="fail" />{{ nameMassage }}
      </p>
    </van-cell-group>
    <van-cell-group>
      <van-field
        class="filed-cell"
        v-model="mobile"
        type="tel"
        maxlength="11"
        label="电话"
        placeholder="请输入收货人联系电话"
        :rules="telRules"
        :show-error-message="false"
      />
      <!-- <p class="error" v-show="telRules-message !== ``"><van-icon name="fail" />{{ telRules.message }}</p> -->
    </van-cell-group>
    <van-cell-group>
      <van-field
        class="filed-cell"
        v-model="adress"
        rows="2"
        label="地址"
        type="textarea"
        placeholder="请输入收货地址"
        :rules="adressRules"
      />
      <p class="error" v-show="adressMassage !== ``">
        <van-icon name="fail" />{{ adressMassage }}
      </p>
    </van-cell-group>
    <div class="btn-box">
      <van-button
        type="primary"
        :color="
          adress !== `` || mobile !== `` || namecell !== ``
            ? 'linear-gradient(to right, #ff6034, #ee0a24)'
            : '#D5D7DA'
        "
        round
        size="small"
        native-type="submit"
        >提交</van-button
      >
    </div>
  </van-form>
</template>

<script>
import Vue from "vue";

import { Field, Form, cell, CellGroup, Button } from "vant";
Vue.use(Field).use(Form).use(cell).use(CellGroup).use(Button);
import { checkMobile } from "../util/util";

export default {
  name: "FormNormal",
  data() {
    return {
      mobile: "",
      namecell: "",
      adress: "",
      // 校验手机号码
      telRules: [
        {
          required: true,
          message: "手机号码不能为空",
          trigger: "onBlur/onChange",
        },
        {
          // 自定义校验规则
          validator: (value) => {
            return /^(0|86|17951)?(13[0-9]|15[012356789]|166|17[3678]|18[0-9]|14[57])[0-9]{8}$/.test(
              value
            );
          },
          message: "请输入正确格式的手机号码",
          trigger: "onBlur/onChange",
        },
      ],
      nameRules: {
        required: true,
        message: "姓名不能为空",
        trigger: "onBlur/onChange",
      },
      adressRules: {
        required: true,
        message: "地址不能为空",
        trigger: "onBlur/onChange",
      },
    };
  },
  methods: {
    onSubmit(values) {
      //  if(this.phone == ""){
      //    console.log("ss")
      //     this.phoneMassage = '手机号不能为空';
      //     return
      //  }else if(!checkMobile(this.phone)) {
      //     this.phoneMassage = '请填写正确的手机号'
      //     return
      //   }
    },
    //保存邮寄地址
    saveAddress() {
      API.POST_SAVE_ADDRESS({ rldid: 0, mobile: "", name: "", address: "" })
        .then(({ returncode, result, message }) => {
          console.log("saveAddress - > ", returncode, result);
          if (+returncode === 0) {
            this.myPrizeList = result;
          } else {
            Toast(message);
          }
        })
        .catch((err) => {
          console.log("saveAddress - 报错 - >", err);
        });
    },
  },
};
</script>

<style lang="scss">
.van-form {
  .van-hairline--top-bottom::after,
  .van-hairline-unset--top-bottom::after {
    border-width: 0;
  }
  .van-cell.filed-cell {
    padding-left: 20px;
    padding-right: 20px;

    &::after {
      left: 0;
      right: 0;
    }
  }
  .cell-error {
    .van-field__control {
      color: #fe4a3b;
    }
  }

  .error {
    padding: 6px 20px;
    background: #fe4a3b;
    color: #fff;
    line-height: 1;
    font-size: 13px;
    font-weight: normal;
    .van-icon {
      background: rgba(255, 255, 255, 0.2);
      border-radius: 100%;
      padding: 1px;
      vertical-align: -2px;
      margin-right: 7px;
    }
  }
  .btn-box {
    padding: 20px 0 30px;
    text-align: center;
    .van-button--small {
      min-width: 150px;
      box-sizing: border-box;
    }
  }
  .van-field__error-message {
    padding: 6px 20px;
    background: #fe4a3b;
    color: #fff;
    line-height: 1;
    font-size: 13px;
    font-weight: normal;
    margin: 10px -20px -10px -120px;
    &::before {
      content: "\F056";
      position: relative;
      display: inline-block;
      font: normal normal normal 14px/1 vant-icon;
      font-size: inherit;
      text-rendering: auto;
      -webkit-font-smoothing: antialiased;
      background: rgba(255, 255, 255, 0.2);
      border-radius: 100%;
      padding: 1px;
      vertical-align: -2px;
      margin-right: 7px;
    }
  }
}
</style>


