<template>
  <section class="prize-exchange">
    <div class="prize-title">
      <h2>兑奖中心</h2>
      <span>我的奖品<van-icon name="arrow" /></span>
      <span>我的车标<van-icon name="arrow" /></span>
    </div>
    <div class="prize-list">
      <van-grid :border="false" :column-num="3">
        <van-grid-item v-for="(item, index) in PrizeData" :key="index">
          <van-image :src="item.picurl" />
          <div class="text">需{{ item.num }}个{{ item.carlogo }}</div>
          <div class="btn outstock" v-if="(item.isexchange = 0)">已兑完</div>
          <div
            class="btn disabled"
            v-else-if="(item.isexchange = 1 && item.renum < item.num)"
          >
            车标不够
          </div>
          <div class="btn" v-else @click="openChangePop(item)">立即兑换</div>
        </van-grid-item>
      </van-grid>
    </div>
    <van-popup
      v-model="showChangePop"
      closeable
      position="bottom"
      class="change-pop"
    >
      <div class="change-title"><img src="./pop-title01.png" /></div>
      <van-grid :border="false" :column-num="4">
        <van-grid-item v-for="(item, index) in AutoLogosList" :key="index">
          <van-image :src="item.logo" />
          <div class="text">{{ item.carlogo }}x{{ item.num }}</div>
        </van-grid-item>
      </van-grid>
      <div class="prize-info">
        <img :src="ChangePrizeInfo.picurl" alt="" />
        <div class="text">
          <h3>{{ ChangePrizeInfo.name }}</h3>
          <p>
            兑换需要{{ ChangePrizeInfo.num }}个{{ ChangePrizeInfo.carlogo }}
          </p>
        </div>
        <div class="prize-btn" @click="exChangePrize(ChangePrizeInfo)">
          确认兑换
        </div>
      </div>
    </van-popup>
    <van-popup
      v-model="showVirtualPrizePop"
      closeable
      position="bottom"
      class="virtual-prize"
    >
      <div class="change-title">
        <img src="//x.autoimg.cn/2scimg/m/topic/20210226/pop-title01.png" />
      </div>
      <div class="change-successful-exchange">
        <h3><van-icon name="checked" />兑换成功</h3>
        <p>可前往「我的奖品」查看</p>
      </div>
      <div class="screenshot">
        <img src="//x.autoimg.cn/2scimg/m/topic/20210226/screenshot.png" />
      </div>
      <div class="prize-btn" @click="goToPrizeList">去看看</div>
    </van-popup>
  </section>
</template>
<script>
import { API } from "../../../api/BlindBox";
import Vue from "vue";
import { Image as VanImage, Grid, GridItem, Toast, Icon, Popup } from "vant";
Vue.use(VanImage).use(Grid).use(GridItem).use(Toast).use(Icon).use(Popup);
export default {
  name: "PrizeExchange",
  props: {
    cityId: {
      type: Number,
      default: 0,
    },
    ispc: Boolean,
  },
  data() {
    return {
      PrizeData: [],
      showChangePop: false,
      showVirtualPrizePop: false,
      AutoLogosList: [
        {
          logo: "//x.autoimg.cn/2sc/2021/2021-2/copper-carlogo.png",
          carlogoid: 1,
          carlogo: "铜器车标",
          num: 0,
        },
        {
          logo: "//x.autoimg.cn/2sc/2021/2021-2/silver-carlogo.png",
          carlogoid: 2,
          carlogo: "银器车标",
          num: 0,
        },
        {
          logo: "//x.autoimg.cn/2sc/2021/2021-2/gold-carlogo.png",
          carlogoid: 3,
          carlogo: "黄金车标",
          num: 0,
        },
        {
          logo: "//x.autoimg.cn/2sc/2021/2021-2/drill-carlogo.png",
          carlogoid: 4,
          carlogo: "钻石车标",
          num: 0,
        },
      ],
      ChangePrizeInfo: {},
    };
  },
  mounted() {
    this.getProdList();
  },
  methods: {
    // 奖品列表
    getProdList() {
      API.GET_PROD_LIST({
        activitype: 1,
      })
        .then(({ returncode, result }) => {
          if (returncode == 0) {
            this.PrizeData = result;
            this.PrizeData = [
              {
                id: 1,
                name: "购物代金券",
                picurl: "//x.autoimg.cn/2scimg/m/20190422/shop_default.png",
                num: 21,
                carlogoid: 1,
                carlogo: "奖品1",
                renum: 0,
                isexchange: 0,
              },
              {
                id: 2,
                name: "购物代金券",
                picurl: "//x.autoimg.cn/2scimg/m/20190422/shop_default.png",
                num: 32,
                carlogoid: 2,
                carlogo: "奖品2",
                renum: 50,
                isexchange: 1,
              },
            ];
          }
        })
        .catch((err) => {
          console.log("Err GET_PROD_LIST:", err);
        });
    },
    //兑换弹窗
    openChangePop(item) {
      this.showChangePop = true;
      this.ChangePrizeInfo = item;
      this.AutoLogosList.forEach((ite) => {
        if (ite.carlogoid === item.carlogoid) {
          ite.num = item.num;
        } else {
          ite.num = 0;
        }
      });
    },
    // 车标兑换商品
    exChangePrize(item) {
      if (item.isreal) {
        //实物
      } else {
        //非实物
        API.POST_EXCHANGE_PRIZE({ prodid: item.id, activitype: 1 })
          .then(({ returncode, message }) => {
            if (returncode != 0) {
              this.showChangePop = false;
              this.showVirtualPrizePop = true;
            } else {
              Toast.fail(message);
            }
          })
          .catch((err) => {
            console.log("Err GET_PROD_LIST:", err);
          });
      }
    },
    //查看奖品列表
    goToPrizeList() {
      this.showVirtualPrizePop = false;
      this.$router.push({
        path: "jiangpin",
        // query: { hmaid: result.hmaid, userid: userid, istwo: 1,needlogin:true},
      });
    },
  },
};
</script>
<style lang="scss">
.prize-exchange {
  margin: 24px 10px 20px;
  background-color: #fff;
  border-radius: 6px;
  overflow: hidden;
  box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.1);
}
.prize-title {
  color: #fff;
  padding: 0 0 0 20px;
  line-height: 40px;
  height: 40px;
  background-image: linear-gradient(130deg, #ffb871 0%, #fe9834 100%);
  h2 {
    display: inline-block;
    font-size: 19px;
    font-weight: 600;
    font-family: MF-FangHei-Regular;
  }
  span {
    float: right;
    font-family: PingFangSC-Semibold;
    font-size: 12px;
    color: #ffffff;
    font-weight: 600;
    vertical-align: top;
    padding: 0 15px;
    position: relative;
    &:last-child::before {
      content: "";
      position: absolute;
      right: 0;
      top: 13px;
      width: 1px;
      height: 12px;
      background-color: #ffdba1;
    }
    .van-icon-arrow {
      vertical-align: top;
      margin-top: 14px;
    }
  }
}
.prize-list {
  padding: 8px;
  .van-grid-item__content {
    background-color: transparent;
    color: #0cfff9;
    padding: 20px 0px;
    .van-image img {
      width: 100px;
      height: 78px;
    }
    .text {
      font-family: PingFangSC-Regular;
      font-size: 12px;
      color: #000000;
      text-align: center;
      font-weight: 400;
      padding-top: 10px;
    }
    .btn {
      margin-top: 10px;
      width: 80px;
      height: 28px;
      line-height: 28px;
      text-align: center;
      display: inline-block;
      font-size: 13px;
      color: #ffffff;
      background-image: linear-gradient(179deg, #ffd75f 0%, #ffa11c 99%);
      border-radius: 235px;
      box-shadow: 0px 3px 10px 0px rgba(0, 0, 0, 0.2);
      &.outstock {
        background: #fff;
        box-shadow: none;
        color: #999999;
      }
      &.disabled {
        background-image: linear-gradient(179deg, #dfdfdf 0%, #a7a7a7 100%);
      }
    }
  }
}
.change-pop {
  background: #f2f2f2;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  overflow: initial;
  .van-popup__close-icon--top-right {
    top: 12px;
    right: 12px;
  }
  .van-icon-cross {
    color: #333;
  }
  .van-grid {
    margin: 0px 15px 15px;
    .van-grid-item__content {
      background-color: transparent;
      font-family: PingFangSC-Regular;
      font-size: 13px;
      color: #666666;
      line-height: 18px;
      padding: 14px 0;
      .van-image img {
        width: 72px;
      }
    }
    .text {
      padding-top: 10px;
      white-space: nowrap;
    }
  }
}
.prize-info {
  background-color: #fff;
  margin: 15px;
  background: #ffffff;
  border-radius: 8px;
  padding: 20px 30px;
  img {
    width: 60px;
    height: 60px;
    float: left;
  }
  .text {
    padding-left: 90px;
    line-height: 1.7;
    height: 78px;
    h2 {
      font-family: PingFangSC-Semibold;
      font-size: 15px;
      color: #1b1b1b;
      letter-spacing: 0.38px;
      font-weight: 600;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
    p {
      font-family: PingFangSC-Regular;
      font-size: 14px;
      color: #666666;
      letter-spacing: 0;
      font-weight: 400;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
  }
}
.prize-btn {
  width: 150px;
  height: 40px;
  margin: 0 auto;
  line-height: 40px;
  background-image: linear-gradient(0deg, #ff5338 0%, #ff985f 100%);
  border-radius: 20px;
  font-family: PingFangSC-Semibold;
  font-size: 16px;
  color: #ffffff;
  letter-spacing: 0;
  text-align: center;
  font-weight: 600;
}
.change-title {
  width: 100%;
  position: relative;
  img {
    width: 100%;
    transform: translateY(-20%);
  }
}
.virtual-prize {
  overflow: initial;
  padding-bottom: 30px;
  .van-popup__close-icon--top-right {
    top: 12px;
    right: 12px;
  }
  .van-icon-cross {
    color: #333;
  }
  .screenshot {
    text-align: center;
    padding: 15px 0 25px;
    img {
      width: 75%;
    }
  }
}
.change-successful-exchange {
  line-height: 1.6;
  padding-top: 10px;
  h3 {
    font-family: PingFangSC-Semibold;
    font-size: 18px;
    color: #1b1b1b;
    letter-spacing: 0.45px;
    font-weight: 600;
    text-align: center;
    .van-icon-checked {
      color: #45c186;
      font-size: 20px;
      vertical-align: top;
      margin: 3px 5px 0 0;
    }
  }
  p {
    font-family: PingFangSC-Regular;
    font-size: 14px;
    color: #666666;
    letter-spacing: 0.35px;
    text-align: center;
    font-weight: 400;
  }
}
</style>


