import SCHEME from '../api/publicScheme';
import { Province_2sc, City_2sc } from '../data/area';
import appbridge from '../util/appbridge';

//根据pid获取pname
function getPnameFromPid(pid) {
  let pname = ''
  if (pid === '' || pid === '0') {
    pname = '全国';
  }
  Province_2sc.map((item) => {
    item.V.map((item2 => {
      if (item2.V === pid + '') {
        pname = item2.T;
      }
    }))
  });
  return pname;
};

//根据市id获取城市名称
function getCnameFromCid(cid) {
  let cname = '';
  if (!cid) {
    cname = '';
  } else {
    let cityInfo = City_2sc.filter((item => {
      if (item.V === cid + '') {
        return item.T;
      }
    }));
    if (cityInfo.length > 0) {
      cname = cityInfo[0].T.split(' ')[1];
    }
  }

  return cname;
}

function domAppendJs(src, callback, id) {
  //判断是否已存在js
  if (id && document.getElementById(id)) {
    callback && callback();
    return;
  }
  // dom注入登录js
  var script = document.createElement('script'),
    head = document.getElementsByTagName('head')[0];
  script.type = 'text/javascript';
  script.charset = 'UTF-8';
  script.src = src;

  if (id) {
    script.id = id
  }
  if (script.addEventListener) {
    script.addEventListener('load', function () {
      callback && callback();
    }, false);
  } else if (script.attachEvent) {
    script.attachEvent('onreadystatechange', function () {
      var target = window.event.srcElement;
      if (target.readyState == 'loaded') {
        callback && callback();
      }
    });
  }
  head.appendChild(script);
}

function domAppendCss(filename) {
  var fileref = document.createElement("link")
  fileref.setAttribute("rel", "stylesheet")
  fileref.setAttribute("type", "text/css")
  fileref.setAttribute("href", filename)
  if (typeof fileref != "undefined") {
    document.getElementsByTagName("head")[0].appendChild(fileref)
  }
}


const UA = navigator.userAgent.toLocaleLowerCase();

//是否在车智赢app中
function isATCApp() {
  return UA.indexOf('autotradercloud') > -1;
}
//是否是二手车app
function isEscApp() {

  return UA.indexOf('che_android') > -1 || UA.indexOf('che_ios') > -1;
}
//是否是主软 app
function isManApp() {
  return UA.indexOf('auto_android') > -1 || UA.indexOf('auto_iphone') > -1
}
//微信中打开
function isWechat() {
  return UA.indexOf('micromessenger') > -1
}
//验证手机号码
function checkMobile(mobile) {
  var regPhone = new RegExp("^1[3,4,5,6,7,8,9]{1}[0-9]{9}$", "ig");
  return (mobile && mobile.length === 11 && regPhone.test(mobile));
}

//车源列表页面跳转
//brandid-品牌id, brandname-品牌名称, seriesid-车系id, sname-车系名称, pid-省份id, cid-城市id, pvid-PV, url-链接, ispc-是否pc（0/1）, infoids = ''//车型id, isoutsite, sourceid：1：个人，2：商家，3：认证商家，20：平台车, priceregion = ''-价格区间,agerange=''车龄区间,mileage=''里程区间,
//infoids必须是字符串，如果是多个以 ,号分开 isoutsite=81800 是818车源,sourceid 4s点直卖、
function openListUlr(brandid, brandname, seriesid, sname, pid, cid, pvid, url, ispc, infoids = '', isoutsite, sourceid, priceregion = '',agerange='',mileage='',fromtype='') {
  let jumpUrl = '';
  if (isEscApp()) {
    let para = {
      "title": "列表页",
      "pid": pid + "",
      "cid": cid + "",
      "brandid": brandid + "",
      "seriesid": seriesid + "",
      "pvareaid": pvid + "",
    }
    if (+isoutsite > 0) {
      para.isoutsite = `${isoutsite}`;
    }
    if (+sourceid > 0) {
      para.sourceid = `${sourceid}`;
    }
    if (`${infoids}`.length > 0) {
      para.infoid = `${infoids}`;
    }
    if (priceregion.length > 0) {
      para.priceregion = priceregion;
    }
    //添加车龄传参
    if (agerange.toString().length > 0) {
      para.registeageregion = agerange;
    }
    //添加里程传参
    if (mileage.toString().length > 0) {
      para.mileageregion = mileage;
    }
    if (+fromtype > 0) {
      para.fromtype = `${fromtype}`;
    }
    jumpUrl = `${SCHEME.USEDCAR}/rnhomegobuycar?param=${encodeURIComponent(JSON.stringify(para))}`;
    console.log(jumpUrl+'--'+JSON.stringify(para));
    // return;
  } else if (isManApp()) {
    if (brandid > 0) {
      var params = [];
      params.push(`pvareaid=${pvid}`)
      var brand = { "brandid": brandid + "", "bname": brandname }
      if (+seriesid > 0) {
        brand.seriesid = seriesid;
        brand.sname = sname;
      }
      params.push(`brand=${encodeURIComponent(JSON.stringify(brand))}`)
      if (+isoutsite > 0) {
        params.push(`service=${isoutsite}`)
      }
      if (+sourceid > 0) {
        params.push(`sourceid=${sourceid}`)
      }
      jumpUrl = `${SCHEME.AUTOHOME_USEDCAR}/buycarlist?${params.join("&")}`
    } else {
      if (+isoutsite > 0) {
        jumpUrl = `${SCHEME.AUTOHOME_USEDCAR}/buycarlist?service=${isoutsite}&pvareaid=${pvid}`;
      } else {
        jumpUrl = `${SCHEME.AUTOHOME_USEDCAR}/buycarlist?pvareaid=${pvid}`;
      }
    }
    if (priceregion.length > 0) {
      jumpUrl = jumpUrl + `&price=${priceregion}`;
    }
    //添加车龄传参
    if (agerange.toString().length > 0) {
      jumpUrl += `&agerange=${agerange}`
    }
    //添加里程传参
    if (mileage.toString().length > 0) {
      jumpUrl += `&mileage=${mileage}`
    }
    //添加商家类型：1：个人，2：商家，3：认证商家，20：平台车
    if (+sourceid > 0) {
      jumpUrl += `&sourceid=${sourceid}`
    }
    //添加inoif传参
    let topids = '';
        if (infoids.toString().length > 0) {
      topids = `&topids=${infoids}`;
    }
    //主软里不用传cid参数，跳转之前需要从新设置主软件cid
    jumpUrl += `${topids}`;
    console.log(jumpUrl);
    // return;
  } else {
    jumpUrl = url;
  }
  console.log('jumpURL - ', jumpUrl);
  // return;
  if (ispc == 1) {
    window.open(jumpUrl);
  } else {
    location.href = jumpUrl;
  }
}

//获取打开列表页的价格筛选条件
function getPriceRegion(minPrice, maxPrice) {
  // m&pc 0_5 50_0
  let priceRegion = `${minPrice}_${maxPrice}`
  //二手车app 0-5 50-65535
  if (isEscApp()) {
    priceRegion = `${minPrice}-${maxPrice}`;
    if (maxPrice === 0) {
      priceRegion = `${minPrice}-65535`
    }
  }
  //内嵌app 0-5 5-9999
  if (isManApp()) {
    priceRegion = `${minPrice}-${maxPrice}`;
    if (maxPrice === 0)
      priceRegion = `${minPrice}-9999`;
  }
  return priceRegion;
}

//获取 URL 参数
function getUrlParams() {
  var search = location.search; //获取url中"?"符后的字串
  var querys = {};
  if (search.indexOf("?") != -1) {
    var params = search.substr(1).split("&");
    for (var i = 0; i < params.length; i++) {
      var pair = params[i].split("=");
      if (pair.length <= 1) continue;
      querys[pair[0]] = pair[1];
    }
  }
  return querys;
}
//添加url参数
function addUrlParams(name, value) {
  var url = window.location.href;
  var newUrl = "";
  var reg = new RegExp("(^|)" + name + "=([^&]*)(|$)");
  var tmp = name + "=" + value;
  if (url.match(reg) != null) {
      newUrl = url.replace(eval(reg), tmp);
  }
  else {
      if (url.match("[\?]")) {
          newUrl = url + "&" + tmp;
      }
      else {
          newUrl = url + "?" + tmp;
      }
  }
  return newUrl;
}
