//cid转pid
function cid2pid(cid) {
  return parseInt(+cid / 10000) * 10000;
}

//给数组排重
function uniqueArr(arr) {
  return arr.filter(function (item, index, arr) {
    //当前元素，在原始数组中的第一个索引==当前索引值，否则返回当前元素
    return arr.indexOf(item, 0) === index;
  });
}

//时间差 毫秒@date1 当前日期对象  @date2日期对象
function timeDiff(date1, date2) {
  console.log(date1.getTime(), date2.getTime())
  return Math.floor((date2.getTime() - date1.getTime()));
}

//获取经纬度
function getGeolocation() {
  let result = { message: '', returncode: 0, res: { lon: 0, lat: 0 } };
  return new Promise((resolve, reject) => {
    if (isManApp()) {
      //主软
      AHAPP.invokeNative("realtimelocation", {
        success: function (res) {
          result.res.lat = res.result.BDLatitude;
          result.res.lon = res.result.BDLongitude;
          // log(JSON.stringify(res)); // 打印客户端返回的参数内容
          resolve(result);
        },
        fail: function (res) {
          // log(JSON.stringify(res));
          result.returncode = 100;
          result.message = JSON.stringify(res);
          reject(result);
        },
      });
    } else if (isEscApp()) {
      //二手车
      appbridge.invoke('intentGeo', {}, (res) => {
        if (res) {
          result.res.lat = res.latitude;
          result.res.lon = res.longitude;
          resolve(result);
        } else {
          resolve('');
        }
      })
    } else {
      //H5
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition((position) => {
          result.res.lat = position.coords.latitude;
          result.res.lon = position.coords.longitude;
          resolve(result);
        }, (error) => {
          console.log(error);
          let message = '';
          switch (error.code) {
            case error.PERMISSION_DENIED:
              message = "您拒绝对获取地理位置的请求";
              break;
            case error.POSITION_UNAVAILABLE:
              message = "位置信息是不可用的";
              break;
            case error.TIMEOUT:
              message = "请求您的地理位置超时";
              break;
            case error.UNKNOWN_ERROR:
              message = "未知错误";
              break;
          }
          result.returncode = 20;
          result.message = message;
          reject(result);
        });
      } else {
        result.returncode = 100;
        result.message = "您的浏览器不支持使用HTML 5来获取地理位置服务";
      }
    }
  });
}

//获取重复字符串
function repeatStringNumTimes(str, num) {
  let repeatStr = '';
  for (let i = 0; i < num; i++) {
    repeatStr += str;
  }
  return repeatStr;
}

function getBrowser() {
  const ua = navigator.userAgent;
  return {
    trident: ua.indexOf("Trident") > -1, // IE内核
    presto: ua.indexOf("Presto") > -1, // opera内核
    webKit: ua.indexOf("AppleWebKit") > -1, // 苹果、谷歌内核
    gecko: ua.indexOf("Gecko") > -1 && ua.indexOf("KHTML") === -1, // 火狐内核
    mobile: !!ua.match(/AppleWebKit.*Mobile.*/), // 是否为移动终端
    ios: !!ua.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/), // ios终端
    android: ua.indexOf("Android") > -1 || ua.indexOf("Adr") > -1, // android终端
    iPhone: ua.indexOf("iPhone") > -1, // 是否为iPhone或者QQHD浏览器
    iPad: ua.indexOf("iPad") > -1, // 是否iPad
    webApp: ua.indexOf("Safari") === -1, // 是否web应该程序，没有头部与底部
    weixin: ua.indexOf("MicroMessenger") > -1, // 是否微信 （2015-01-22新增）
    qq: ua.match(/\sQQ/i) === " qq", // 是否QQ
    escApp: ua.indexOf("UsedCar") > -1, // 二手车APP
    autohome: ua.indexOf("autohomeapp") > -1 // 主软
  };
}
function getAPPID() {
  const os = getBrowser();
  let _appid = "pc";
  if (os.mobile) {
    switch (true) {
      case os.autohome && os.iPhone:
        _appid = "app.iphone";
        break;
      case os.autohome && os.iPad:
        _appid = "app.ipad";
        break;
      case os.autohome && os.android:
        _appid = "app.android";
        break;
      case os.iPhone:
        _appid = "m.iphone";
        break;
      case os.android:
        _appid = "m.android";
        break;
      default:
        _appid = "m.iphone";
    }
  }
  return _appid;
}

//获取维一id
function getUniqueid() {
  return new Promise((resolve, reject) => {
    if (isEscApp()) {
      appbridge.invoke('invokePublicParam', {}, (data) => {
        if (data && data.deviceid) {
          resolve(data.deviceid);
        } else {
          resolve('');
        }
      })
    } else if (isManApp()) {
      AHAPP.invokeNative("getdeviceinfo", {
        success: function (data) {
          console.log(data);
          if (data && data.returncode === 0 && data.result.deviceid) {
            resolve(data.result.deviceid);
          } else {
            resolve('');
          }
        },
        fail: function (result) {
          resolve('');
        }
      });
    } else {
      let uid = $.getCookie('sessionid', '');
      resolve(uid);
    }
  });
}
Date.prototype.Format = function (fmt) {
  var o = {
    "M+": this.getMonth() + 1, //月份 
    "d+": this.getDate(), //日 
    "h+": this.getHours(), //小时 
    "m+": this.getMinutes(), //分 
    "s+": this.getSeconds(), //秒 
    "q+": Math.floor((this.getMonth() + 3) / 3), //季度 
    "S": this.getMilliseconds() //毫秒 
  };
  if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (this.getFullYear() + "").substr(4 - RegExp.$1.length));
  for (var k in o)
    if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
  return fmt;
}

//取cookie方法,原$.getCookie() 有缺陷
function getCookie(name)
{
var arr,reg=new RegExp("(^| )"+name+"=([^;]*)(;|$)");
if(arr=document.cookie.match(reg))
{return unescape(arr[2])}
else
{return ''};
}
