<template>
  <section class="rightMenu">
    <div class="rightShare">
      <div class="btn" id="shareBtn" @click="shareClick"
      :data-clipboard-text="`【来汽车之家二手车抽盲盒，2021购车金等你拿！${
        mixin_share_params.options.url.split('hmaid')[0]
      }】`"
      ><span class="name">分享</span><span class="circle"><van-icon name="arrow" /></span></div>
      <div class="btn"> <span class="name">规则</span><span class="circle"><van-icon name="arrow" /></span></div>
    </div>
    <van-popup
      v-model="isShowWxSharePop"
      position="top"
      duration="0"
      class="wxTipPop"
      ><img
        class="weixin-share-tip"
        src="//x.autoimg.cn/2sc/2021/2021-1/new-year-weixin-share-tip-2.png"
    /></van-popup>
  </section>
</template>

<script>
import {
  NavBar,
  Toast,
  Icon,
  Popup,
} from "vant";

import Vue from "vue";
Vue.use(NavBar)
  .use(Toast)
  .use(Icon)
  .use(Popup);
import { Mixin_Share } from "../../../util/mixins/mixin_share";
export default {
  name: "rightMenu",
  components: {
  },
  mixins:[Mixin_Share],
  props: {
    ispc: Boolean,
    isLogin: Boolean
  },
  data() {
    const url = location.href.split("?")[0] + "?pvareaid=110480";
    const title = "我在汽车之家二手车抽半价车，快助我一臂之力！";
    const imageUrl = "https://x.autoimg.cn/2sc/2021/2021-1/wechart-share.png";
    return {
      mixin_share_params:{
        options:{
          title,
          pageTitle: "来汽车之家二手车抽盲盒，2021购车金等你拿！",
          content: "来汽车之家二手车抽盲盒，2021购车金等你拿！",
          imageUrl,
          url,
          target: "#shareBtn",
          platform: "weixin&weixincircle&weibo&qqzone",
          callback: this.shareCallBack,
          autojsxcx: true, //二手车分享好友到小程序卡片
          ahOptions: {
            wx_mini_program: {
              userName: 'gh_789f6dd85253',
              type: 0, //要跳转的小程序对应的版本，选填 0：正式版本（默认） 1：测试版本 2：体验版本
              path: `webview/che168/index?url=${encodeURIComponent(url)}&needlogin=true&sharetitle=${title}&shareimage=${encodeURIComponent(imageUrl)}`
            }
          },
        },
        isLogin:false,//登录
        ispc:false  //是否PC
      },
      isShowWxSharePop:false, //小程序分享
      //分享callback  error {"1":"是pc","0","没登陆"}
      //分享callback  success {"1":"小程序中","2":"主软APP和二手车APP","3":"M或者PC"}
    };
  },
  created(){
  },
  mounted() {
    this.initData();
    this.$eventBus.$emit("bb_mixin_share_params", this.mixin_share_params);
  },
  methods: {
    initData() {
    },
    shareClick(){
      this.mixin_share_shareBtnClick().then(res=>{
        console.log(res)
        if(res.code == "1"){
          this.isShowWxSharePop = true;
        }else if(res.code == "2"){
          window.fzshare.clickShare();
        }else if(res.code == "3"){
          this.mpcShare();
        }
      }).catch(err=>{
        if(err.code == "0"){
          this.goLogin();
        }else if(err.code == "1"){
          Toast("请前往二手车之家APP参与活动");
          return;
        }
        console.error(err)
      })
    },
    //mpc 分享
    mpcShare() {
      var clipboard = new Clipboard("#shareBtn");
      clipboard.on("success", function (e) {
        Toast("【活动链接已复制，快去分享给好友吧】");
      });
    },
    //app获取分享结果
    shareCallBack(data) {
      console.log("shareCallBack - > ", data);
    },
    //去登录
    goLogin() {
      console.log(this.ispc);
      if (this.ispc) {
        location.href = `//account.che168.com/login?backurl=${location.href}`;
      } else {
        window.setTimeout(function () {
          oneKey.login({
            success: function (data) {
              window.location.href = document.URL;
            },
            error: function (data) {},
          });
        }, 500);
      }
    },
  },
  watch:{
    ispc:{
      immediate:true,
      handler(v){
        this.mixin_share_params.ispc = v;
      }
    },
    isLogin:{
      immediate:true,
      handler(v){
        this.mixin_share_params.isLogin = v;
      }
    },
  }
};
</script>

<style lang="scss" scoped>
  .rightMenu{
    width:20px;
    color:#fff;
    .rightShare{
      .btn{
      margin-top:11px;
      width: 22px;
      height: 58px;
      background-image: linear-gradient(0deg, #FFAF5A 0%, #FD8B1A 100%);
      border: 1px solid #DCF3FF;
      border-radius: 4px 0 0 4px;
      font-family: PingFangSC-Medium;
      font-size: 12px;
      color: #FFFFFF;
      letter-spacing: 0;
      text-align: center;
      font-weight: 500;
      box-sizing: border-box;
      padding: 2px 0;
      .name{
        line-height: 17px;
      }
      .circle{
        border: 1px solid #fff;
        border-radius: 50%;
        width: 12px;
        height: 12px;
        display: inline-block;
        text-align: center;
        margin-top: 2px;
        position: relative;
        box-sizing: border-box;
        vertical-align: middle;
        .van-icon{
          font-size: 12px;
          transform: scale(.8);
          transform-origin: 0 0;
          position: absolute;
          left: 0;
          top: 0;
          text-align: center;
        }
      }
      }
    }
  }
  .wxTipPop {
  background-color: rgba(0, 0, 0, 0);
  .weixin-share-tip {
    width: 156px;
    height: 112px;
    margin: 10px 60px 0 0;
    float: right;
  }
}
</style>


