<template>
  <section class="my-carlogos">
    <div class="logos flex-row-center">
      <div
        class="logo-items flex-colnum-center"
        v-for="(item, index) in autoLogosList"
        :key="index"
      >
        <img :src="item.picurl" />
        <div class="logotitle">{{ item.name }}x{{ item.num }}</div>
      </div>
    </div>

    <div class="btns flex-row-space-around">
      <div class="btn-txt btn1 flex-row-center" @click="btnClick(1)">
        获取更多盲盒
      </div>
      <div class="btn-txt btn2 flex-row-center" @click="btnClick(2)">
        兑换新豪礼
      </div>
    </div>
  </section>
</template>

<script>
//我的车标
import { Popup } from "vant";
import vue from "vue";
vue.use(Popup);

import { API } from "../../../api/BlindBox";

export default {
  name: "”MyAutoLogos”",
  data() {
    return {
      autoLogosList: [
        {
          id: 1,
          name: "铜器车标",
          num: 1,
          usednum: 2,
        },
        {
          id: 2,
          name: "银器车标",
          num: 2,
          usednum: 2,
        },
        {
          id: 3,
          name: "黄金车标",
          num: 3,
          usednum: 2,
        },
        {
          id: 4,
          name: "钻石车标",
          num: 4,
          usednum: 2,
        },
      ], //我的车标
    };
  },
  components: {},
  props: {},
  created() {
    this.getMyAutoLogos();
  },
  mounted() {},
  methods: {
    //我的车标
    getMyAutoLogos() {
      API.GET_MUCAR_LOGO_LIST({})
        .then(({ returncode, result }) => {
          console.log("getMyAutoLogos - > ", returncode, result);
          if (+returncode === 0) {
            this.autoLogosList = result.carlogos;
          }
        })
        .catch((err) => {
          console.log("getMyAutoLogos - 报错 - >", err);
        });
    },

    //按钮点击
    btnClick(type) {
      if (type === 1) {
        this.$eventBus.$emit("topic-manghe-open-task-list", { isShow: true });
      } else if (type === 2) {
        this.$eventBus.$emit("topic-manghe-isshow-myautologs", {
          isShow: false,
        });
      }
    },
  },
};
</script>

<style lang="scss">
.my-carlogos {
  padding: 5px 32px;
  background-color: #ffffff;
  .logos {
    width: 100%;
    .logo-items {
      width: 25%;
      .logotitle {
        font-size: 13px;
        color: #666666;
        letter-spacing: 0.32px;
        text-align: center;
        line-height: 18px;
        font-weight: 400;
        margin-top: 10px;
      }
      img {
        width: 80%;
      }
    }
  }

  .btns {
    margin: 40px auto;
    .btn-txt {
      font-size: 16px;
      color: #ffffff;
      letter-spacing: 0;
      text-align: center;
      font-weight: 600;
    }
    .btn1 {
      width: 150px;
      height: 40px;
      background-image: linear-gradient(179deg, #ffd75f 0%, #ffa11c 99%);
      border-radius: 235px;
    }
    .btn2 {
      width: 150px;
      height: 40px;
      background-image: linear-gradient(0deg, #ff5338 0%, #ff985f 100%);
      border-radius: 20px;
    }
  }
}
</style>


