<template>
  <section>
    <van-swipe
      class="winning-swipe"
      :autoplay="5000"
      vertical
      :show-indicators="false"
      :touchable="false"
      v-if="winningData.length > 2"
    >
      <van-swipe-item v-for="(item, index) in winningData" :key="index">
        <img :src="item.img" alt="" />{{ item.name }}获得了{{ item.prize }}
      </van-swipe-item>
    </van-swipe>
  </section>
</template>
<script>
import Vue from "vue";
import { Swipe, SwipeItem } from "vant";
Vue.use(Swipe).use(SwipeItem);
import { API } from "../../../api/BlindBox";
export default {
  name: "WinningRounds",
  props: {
    cityId: {
      type: Number,
      default: 0,
    },
    ispc: Boolean,
  },
  data() {
    return {
      winningData: [
        {
          img:
            "http://x.autoimg.cn/2scimg/pc/web/20190123/person-weidenglu-logo.png",
          name: "用户1号",
          prize: "铜器车标铜器车标铜器车标铜器车标铜器车标铜器车标铜器车标铜器车标",
        },
        {
          img:
            "http://x.autoimg.cn/2scimg/pc/web/20190123/person-weidenglu-logo.png",
          name: "用户2号",
          prize: "铜器车标",
        },
        {
          img:
            "http://x.autoimg.cn/2scimg/pc/web/20190123/person-weidenglu-logo.png",
          name: "用户3号",
          prize: "铜器车标",
        },
        {
          img:
            "http://x.autoimg.cn/2scimg/pc/web/20190123/person-weidenglu-logo.png",
          name: "用户4号",
          prize: "铜器车标",
        },
        {
          img:
            "http://x.autoimg.cn/2scimg/pc/web/20190123/person-weidenglu-logo.png",
          name: "用户5号",
          prize: "铜器车标",
        },
      ],
    };
  },
  mounted() {},
  methods: {},
};
</script>

<style lang="scss">
.winning-swipe {
  margin: 0 28px;
  height: 22px;
  background: rgba(108, 230, 255, 0.27);
  border-radius: 105px;
}
.winning-swipe .van-swipe-item {
  width: 100%;
  font-family: PingFangSC-Regular;
  font-size: 14px;
  color: #0cfff9;
  text-align: left;
  font-weight: 400;
  line-height: 22px;
  box-sizing: border-box;
  padding: 0 5px;
  white-space:nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  img {
    width: 15px;
    height: 15px;
    border-radius: 50%;
    vertical-align: top;
    margin: 3px 5px 0 0;
  }
}
</style>


