<template>
  <section class="OpenBlindBox">
    <img style="display:none" src="//x.autoimg.cn/2scimg/m/topic/20210226/bb_box_animation.gif" data-src="" alt="盲盒123123">
    <div class="OpenBlindBoxCont">
      <!-- <img src="//x.autoimg.cn/2scimg/m/topic/20210226/bb_box_animation.gif" alt=""> -->
      <div class="openBlindCont">
        <div class="BlindContWidth">
          <div class="bb_kuang_bg"></div>
          <div class="topTitle">
            <img src="./bb_topTitle.png" alt="开盲盒 攒车标">
          </div>
          <div class="content">
            <div class="content_box" @click="openBox" :class="[{huangdong:huangdongTog}]">
              <img class="box"  src="./bb_box.png" alt="盲盒">
              <img class="hand" src="./bb_hand.png" alt="">
            </div>
          </div>
        </div>
      </div>
      <div class="opportunity" v-if="attr.isLogin">
        <P>还有<span class="num">{{blindBoxNum}}</span>次开盲盒机会</P>
      </div>
      <div class="buttonArea">
        <div class="btn" @click="getMoreBox">获得更多盲盒</div>
      </div>
    </div>
    <!-- 开盲盒动效 -->
    <van-popup class="blindboxPopClass" :close-on-click-overlay="false"
    v-model="blindboxPop">
      <div class="giveNum">
        <div class="animationBg"><img src="./bb_openBox_bg.png" alt=""></div>
        <p :style="{visibility:!blindboxStatus?'unset':'hidden'}"><span>{{`恭喜您获得1个盲盒`}}</span><van-icon @click="blindboxPop=false" name="http://x.autoimg.cn/2sc/2021/2021-1/new-year-weixin-share-tip-2.png" size="25px" /></p>
        <h3><img src="./bb_openBox_bg1.png" alt="AUTOHOME"> </h3>
        <div class="animation">
          <img class="box" v-if="!blindboxStatus" src="./bb_box.png" alt="">
          <img class="box" v-else ref="animationImg" src="" data-src="//x.autoimg.cn/2scimg/m/topic/20210226/bb_box_animation.gif" alt="盲盒">
        </div>
        <van-button :style="{visibility:!blindboxStatus?'unset':'hidden'}" round block type="info" @click="giveBoxFun">立即拆开</van-button>
      </div>
    </van-popup>
    <van-popup :class="[{openBoxResultPop:popStatus == `openBoxResult`,taskPop:popStatus == `task`,'popup-self':popStatus == `task`}]" closeable 
    @close="boxAllPop=false" :close-on-click-overlay="false" :position="popStatus == `task`?'bottom':'center'"
    v-model="boxAllPop" popup-close-icon-size="15px" close-icon="//x.autoimg.cn/2sc/2021/2021-1/zhong/btn_close_black.png">
    <!-- 开盲盒 -->
      <div class="openBoxResult" v-if="popStatus == `openBoxResult`">
        <template v-if="openBoxResult.id">
          <p class="popTitle"></p>
          <p class="carType" >{{openBoxResult.title}}+1</p>
        </template>
        <template v-else>
          <p class="popTitle error"></p>
          <p class="carType error">盲盒空空如也:(</p>
        </template>
        <div style="width:125px;margin:24px auto 0">
          <img style="width:100%" :src="openBoxResult.imageurl" alt="img">
        </div>
        <p class="haveNum"> 
          <span>还有 <em>{{blindBoxNum}}</em> 次开盲盒机会</span>
        </p>
        <van-button v-if="blindBoxNum>0" @click="boxResultNext()">再开一个</van-button>
        <van-button v-else @click="getMoreBox">获取更多盲盒</van-button>
      </div>
      <!-- 任务列表 -->
      <div class="taskList" v-if="popStatus == `task`">
        <div class="pop-cont content">
          <div class="title"><img src="./pop-title02.png" /></div>
          <div class="item">
            <div class="left"><p>今日签到 {{taskStatus.sign?'1':'0'}}/1</p><p>每日签到可获得盲盒1个</p></div>
            <van-button @click="taskHandleFun(1,taskStatus.sign)" :class="[{dis:taskStatus.sign}]">{{taskStatus.sign?`已签到`:`签到`}}</van-button>
          </div>
          <div class="item" v-if="!mixin_share_isInWeiXinLite">
            <div class="left"><p>分享至朋友圈 {{taskStatus.timeline?'1':'0'}}/1</p><p>每日分享至朋友圈可获得盲盒1个</p></div>
            <van-button @click="taskHandleFun(2,taskStatus.timeline)" :class="[{dis:taskStatus.timeline}]">{{taskStatus.timeline?`已分享`:`去分享`}}</van-button>
          </div>
          <div class="item">
            <div class="left"><p>分享微信好友 {{taskStatus.friend?'1':'0'}}/1</p><p>分享给微信好友，可获得盲盒1个</p></div>
            <van-button @click="taskHandleFun(3,taskStatus.friend)" :class="[{dis:taskStatus.friend}]">{{taskStatus.friend?`已分享`:`去分享`}}</van-button>
          </div>
          <div class="item">
            <div class="left"><p>浏览新年梦想车 {{taskStatus.car?'1':'0'}}/1</p><p>前往新春活动页面，浏览任意车源，可获得盲盒1个</p></div>
            <van-button @click="taskHandleFun(4,taskStatus.car)" :class="[{dis:taskStatus.car}]">{{taskStatus.car?`已浏览`:`去浏览`}}</van-button>
          </div>
        </div>
      </div>
    </van-popup>
    <!-- <van-popup class="blindboxPop taskFinishClass" closeable @close="taskFinishPop=false" :close-on-click-overlay="false"
    v-model="taskFinishPop">
      <div class="taskFinishCont">
        <p>{{["签到成功","分享成功","分享成功","看新年梦想车"][taskFinishType-1]}}</p>
        <p>获得1个盲盒</p>
        <div>
          <img src="" alt="动效img">
        </div>
        <p>{{`做任务 得更多盲盒 还有${blindBoxNum}次开盲盒机会`}}</p>
      </div>
    </van-popup> -->
    <van-popup
      v-model="isShowWxSharePop"
      position="top"
      duration="0"
      class="wxTipPop"
      ><img
        class="weixin-share-tip"
        src="./bb_share_guide.png"
    /></van-popup>
  </section>
</template>

<script>
//开盲盒

import {
  NavBar,
  Toast,
  Icon,
  Popup,
  Button 
} from "vant";

import Vue from "vue";
Vue.use(NavBar)
  .use(Toast)
  .use(Icon)
  .use(Button)
  .use(Popup);
  
import { addUrlParams ,getUrlParams} from "../../../util/util";
import { API } from "../../../api/BlindBox";
import { Mixin_Share } from "../../../util/mixins/mixin_share";
// import rightMenu from "./RightMenu.vue"
export default {
  name: "OpenBlindBox",
  mixins:[Mixin_Share],
  components: {
    // rightMenu
  },
  props: {
    ispc: Boolean,
    // isLogin: {
    //   type: Boolean,
    //   default: false, //zanshi zhe
    // },
    attr:Object
  },
  data() {
    return {
      aaa:false,
      blindBoxNum:0,  //总共几次盲盒机会
      blindboxPop:true,  //开盒子动画，和首次
      blindboxStatus:0, //赠送0和开盲盒动画1
      boxAllPop:false, //开盒子礼品
      openBoxResult:{
        "id": 1,
        "title": "铜车标",
        "imageurl": "//x.autoimg.cn/2scimg/m/topic/20190814/prize-2.png"
      },  //盲盒结果
      popStatus:"openBoxResult",//pop区分
      huangdongTog:false, //动销开关
      mixin_share_params:{}, //分享参数
      taskStatus:{ //任务列表得状态
        car: 0,
        friend: 0,
        sign: 0,
        timeline: 0,
      },
      // taskFinishType:"", //任务完成得标识
      // taskFinishPop:false, //任务完成弹框
      isShowWxSharePop:false, //微信和小程序得分享蒙层
      imgLoad:false
    };
  },
  created(){
    // this.preload(); //预加载动效图片
    this.watchBlindBoxNum();
    this.$eventBus.$on("bb_mixin_share_params", (obj) => {
      this.mixin_share_params = obj;
    });
    //打开任务列表 === 需要配合添加id
    this.$eventBus.$on("topic-manghe-open-task-list", (data) => {
      if(data.isShow){
        this.getMoreBox();
      }
    });
  },
  mounted() {
    this.initData();
  },
  methods: {
    initData(){
      this.getBlindBoxNum(true);
      this.changeAnimation();
      //进入页面展开任务列表
      if(getUrlParams()["popStatus"] == 'task'){
        this.popStatus = "task";
        this.boxAllPop=true;
      }
      // else if(getUrlParams()["popStatus"] == 'taskFinish'){ //zhe
      //   this.taskFinishPop=true;
      //   this.taskFinishType = 4; //暂时为看梦想车
      // }
    },
    preload(){
      let img = new Image();
      img.src="//x.autoimg.cn/2scimg/m/topic/20210226/bb_box_animation.gif";
      let that = this;
      img.onload=function(){
        this.imgLoad = true;
      }
      img.onerror = function(){
        this.imgLoad = false;
      }
    },
    //分享 参数type 1：签到 2：分享朋友圈 3：微信好友 4去看车
    shareClick(type){
      this.mixin_share_shareBtnClick().then(res=>{
        console.log(res)
        if(type == 2){
          if(res.code == "1"){
            this.isShowWxSharePop = true;
            setTimeout(()=>{
              this.addBlindBoxNum(2)
            },5000)
          }else if(res.code == "2"){
            setTimeout(()=>{
              this.addBlindBoxNum(2)
            },5000)
            window.fzshare.clickShare();
          }else if(res.code == "3"){
            Toast("请前往二手车之家APP参与活动");
          }
        }else if(type == 3){
          if(res.code == "1"){
            this.isShowWxSharePop = true;
            setTimeout(()=>{
              this.addBlindBoxNum(2)
            },5000)
          }else if(res.code == "2"){
            setTimeout(()=>{
              this.addBlindBoxNum(2)
            },5000)
            window.fzshare.clickShare();
          }else if(res.code == "3"){ //zhe
            Toast("请前往二手车之家APP参与活动");
          }
        }
      }).catch(err=>{
        if(err.code == "0"){
          // this.goLogin(); //zanshi
        }else if(err.code == "1"){
          Toast("请前往二手车之家APP参与活动");
          return;
        }
        console.error(err)
      })
    },
    //赠送次数弹框 立即拆开
    giveBoxFun(){
      this.checkLogin().then(()=>{
        this.addBlindBoxNum(0).then(()=>{
          this.openBox();
        }).catch(()=>{
          Toast("领取失败!")
        })
      })
    },
    //增加盲盒次数 0：赠送 1：签到 2：分享 3：看车
    addBlindBoxNum(type){
      return new Promise((res,rej)=>{
        API.POST_INSERT_BLINDBOX({activitype:1,type:type}).then(({ returncode, result }) => {
          if (returncode == 0) {
            this.getBlindBoxNum(); //刷新盲盒数量
            this.getTaskStatus(); //增加成功后刷新任务状态
            res();
          }else {
            rej();
          }
        })
        .catch((err) => {
          rej();
        });
      })
    },
    //去登录
    goLogin(type) {
      let URL = "";
      if(type){
        URL = addUrlParams("popStatus","task");
      }else{
        URL = location.href;
      }
      if (this.ispc) {
        location.href = `//account.che168.com/login?backurl=${URL}`;
      } else {
        window.setTimeout(function () {
          oneKey.login({
            success: function (data) {
              window.location.href = URL;
              // window.location.href = document.URL;
            },
            error: function (data) {},
          });
        }, 500);
      }
    },
    //获取盲盒数量 ,参数为是否为赠送弹框(也是默认第一次)
    getBlindBoxNum(giveTog) {
        API.GET_BLIND_BOX_NUM({
        }).then(({ returncode, result }) => {
          if (returncode == 0) {
            this.blindBoxNum = result.num;
            if(result.givenum == 0 && giveTog){  //zhe ceshi
              this.blindboxStatus = 0;
              this.blindboxPop = true; //zhe zanshi
              setTimeout(()=>{
                this.blindboxPop=false;
              },5000)
            }
          }else {
            this.blindBoxNum = 0;
          }
        })
        .catch((err) => {
        });
    },
    //操作判断是否登录,否则去登录
    checkLogin(type){
      return new Promise((res,rej)=>{
        if(!this.attr.isLogin){
          // this.goLogin(type); //zhe zanshi
          res(); //zhe zanshi
          // rej();
        }else{
          res()
        }
      })
    },
    //开盲盒
    openBox(){
      this.checkLogin().then(res=>{
        //有次数
        if(this.blindBoxNum > 0){
          this.blindboxPop = true; //动画
          this.blindboxStatus = 1;
          this.$nextTick(function(){
            this.$refs.animationImg.src = "a.png";
            console.error(this.$refs.animationImg.src)
            console.error(this.$refs.animationImg.getAttribute("data-src"))
            this.$refs.animationImg.src = this.$refs.animationImg.getAttribute("data-src");
          })
          setTimeout(()=>{
            this.blindboxPop = false; //动画
            this.blindboxStatus = 0;
            this.boxAllPop = true;
            this.popStatus = `openBoxResult`; //开盲盒
            this.$refs.animationImg.src = "a.png";
          },3000)
          API.GET_LUCKY_DRAW({activitype:1})
          .then(({ returncode, result }) => {
            if (returncode == 0) {
              this.getBlindBoxNum(); //刷新盲盒数量
              this.openBoxResult = result;
            }else {
              Toast("失败")
            }
          })
          .catch((err) => {
          });
        }else{
            Toast("暂无可开的盲盒，快去获取更多盲盒吧！")
        }
      })
    },
    //获取盲盒
    getMoreBox(){
      this.checkLogin("task").then(res=>{
        this.popStatus = "task";
        this.boxAllPop=true;
        this.getTaskStatus();
      })
    },
    //获取task按钮状态
    getTaskStatus(){
      API.GET_TASK_STATUS({activitype:1}).then(({ returncode, result })=>{
        if(returncode == "0"){
          // this.taskStatus = result;//zhe zanshi
        }
      })
    },
    //做盲盒任务  1签到2分享朋友圈3分享好友4看梦想车
    taskHandleFun(type,tog){
      if(tog) return;
      switch(type){
        case 1:
          this.addBlindBoxNum(type).then(()=>{
            Toast("任务完成\n开盲盒机会+1")
          }).catch(()=>{
            Toast("签到失败");
          })
          break;
        case 2:
          this.shareClick(type);
          break;
        case 3:
          this.shareClick(type);
          break;
        case 4:
          openChangeAroundUrl("//topicm.che168.com/TopicApp/2021/kaimenhong/index"); //url 待定
          // this.shareClick(type);
          break;
      }
    },

    //继续开盲盒
    boxResultNext(){
      this.boxAllPop=false;
    },
    //盒子得晃动
    changeAnimation(){
      setInterval(()=>{
        this.huangdongTog = !this.huangdongTog;
      },1500)
    },
    //监听次数操作重新获取
    watchBlindBoxNum(){
      this.$eventBus.$on("che168_blindBoxNum", (data) => {
        this.getBlindBoxNum();
      });
    }
  },
  watch:{
    attr:{
      // immediate:true,
      handler:function(v){
      }
    }
  }
};
</script>

<style lang="scss">

  $aaa:#666;
  .OpenBlindBox{
    width:100%;
    position: relative;
    .OpenBlindBoxCont{
      // padding:0 27.5px;
      position: relative;
    }
    .title-back {
      width: 22px;
      height: 22px;
    }
    .openBlindCont{
      background-image: url(./bb_box_bg.jpg); //zhe zanshi
      background-size: 100% 100%;
      position: relative;
      padding-bottom: 40px;
      .BlindContWidth{
        width:86%;
        position: relative;
        margin:0 auto;
        .bb_kuang_bg{
          width:100%;
          height:100%;
          background-image: url(./bb_kuang_bg.png); //zhe zanshi
          background-size: contain;
          position:absolute;
          background-repeat: no-repeat;
          background-position: center bottom;
        }
        .topTitle{
          width:calc(100% - 24px);
          margin:0 auto 16px;
          img{
            width:100%;
          }
        }
        .content{
          position: relative;
          .content_box{
            width:60%;
            margin:0 auto;
            position: relative;

            .box{
              width:100%;
              transform: scale(1.5) translateY(10px);
            }
            .hand{
              position: absolute;
              left: 50%;
              top: 0;
              /* right: 0; */
              bottom: 0;
              /* margin: auto; */
              width: 70%;
              transform: translate(-50%, 50%);
            }
            &.huangdong{
              animation: LR .2s 0.2s infinite;
            }
          }
  
        }
      }
    }
    .opportunity{
      p{
        font-family: PingFangSC-Semibold;
        font-size: 14px;
        color: #000000;
        letter-spacing: 0.35px;
        text-align: center;
        line-height: 1;
        font-weight: 600;
        .num{
          padding:0 5px;
          color: #fff;
          // -webkit-text-stroke:1px #22E4FD;
          line-height: 1;
          // width: 15px;
          // height: 28px;
          font-family: DINCondensed-Bold;
          font-size: 38px;
          color: #FF8518;
          letter-spacing: 0.95px;
          text-align: center;
          font-weight: 700;
        }
      }
    }
    .buttonArea{
      padding:14px 0 0;
      .btn{
        margin:0 auto;
        width: 290px;
        height: 50px;
        line-height: 48px;
        background-image: linear-gradient(0deg, #7B57FF 15%, #E4BFFF 100%);
        box-shadow: inset 0px 4px 6px 0px rgba(255,182,238,0.57),0 3px 8px rgba(126,126,126,.53);
        // box-shadow: inset 0px 4px 6px 0px rgba(255,182,238,0.57);
        border-radius: 25px;
        font-family: PingFangSC-Semibold;
        font-size: 18px;
        color: #FFFFFF;
        letter-spacing: 0;
        text-align: center;
        font-weight: 600;
      }
    }
    .rightMenu{
      position:absolute;
      right:0;
      top:0;
    }
  }
  .blindboxPopClass {
    text-align: center;
    background-color: transparent;
    width:100%;
    height:100%;
    overflow:hidden;
    .giveNum{
      width:100%;
      height:100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      margin:0 auto;
      .animationBg{
        width:100%;
        height:100%;
        position:absolute;
        top:0;
        left: 0;
        transform: scale(1.5);
        z-index: -1;
        img{
          width:100%;
          position:absolute;
          top:0;
          bottom:0;
          left: 0;
          right:0;
          margin:auto;
          -webkit-animation:rotate 30S linear infinite;
          -moz-animation:rotate 30S linear infinite;
          animation:rotate 30S linear infinite;
        }
      }
      .animation{
        margin-top: -134px;
        width:100%;
        height:83.46vw;
        img{
          width:100%;
        }
      }
      p{
        color: #FFFFFF;
        font-size: 22px;
        line-height: 1;
        position: relative;
        .van-icon{
          position: absolute;
          right: -50px;
          top: -15px;
        }
      }
      h3{
        // padding-top:20px;
        padding:20px 12px 0;
        img{
          width:100%;
        }
      }
      .van-button{
        width: 200px;
        height: 45px;
        background-image: linear-gradient(0deg, #FF5338 0%, #FF985F 100%);
        border-radius: 355px;
        font-family: PingFangSC-Semibold;
        font-size: 16px;
        color: #FFFFFF;
        letter-spacing: 0;
        text-align: center;
        font-weight: 600;
        border:none;
      }

    }
    >>>.van-icon{
      font-size: 12px;
    }
  }
  .openBoxResultPop{
    height:auto;
    width:234px;
    // margin:0 auto;
    // background: #FFFFFF;
    // border-radius: 12px;
    // border-radius: 12px;
    text-align: center;
    padding:32px 0px;
    box-sizing: border-box;
    padding-top: 50px;
    background-color: transparent;
    .van-popup__close-icon{
      width: 25px;
      height: 25px;
      border: 1.5px solid #FFFFFF;
      border-radius: 50%;
      right:0;
      top:0;
      box-sizing: border-box;
    }
    .openBoxResult{
      background:#fff;
      padding-top: 30px;
      border-radius: 8px;
      .popTitle{
        height:40px;
        width:170px;
        background-image:url('./bb_boxResult_success.png');
        background-size: 100% 100%;
        // background-image:url('./bb_boxResult_success.png');
        position:absolute;
        top:38px;
        left:50%;
        transform: translateX(-50%);
        &.error{
          background-image:url(./bb_boxResult_error.png);
          // background-color: #666;
        }
      }
      .carType{
        font-family: PingFangSC-Semibold;
        font-size: 21px;
        color: #FF8018;
        letter-spacing: 0;
        font-weight: 600;
        &.error{
          color: #4A4A4A;
        }
      }
      .haveNum{
        font-family: PingFangSC-Regular;
        height:22px;
        font-size: 12px;
        color: #666666;
        letter-spacing: 0;
        text-align: center;
        font-weight: 400;
        margin-top:25px;
        display: inline-block;
        position: relative;
        span{
          position: relative;
          z-index: 1;
          em{
            font-family: DINAlternate-Bold;
            font-size: 14px;
            color: #262626;
            letter-spacing: 0;
            font-weight: 700;
          }
        }
        &::after{
          content: "";
          width:115%;
          height:14px;
          position:absolute;
          bottom:0;
          background-color: #ECECEC;
          left:50%;
          transform: translateX(-50%);
          border-radius: 3px;
          z-index: 0;
        }

      }
      .van-button{
        width: 170px;
        height: 40px;
        background-image: linear-gradient(0deg, #FF5338 0%, #FF985F 100%);
        border-radius: 20px;
        font-size: 16px;
        color: #FFFFFF;
        letter-spacing: 0;
        text-align: center;
        font-weight: 600;
        transform: translateY(20px);
        border:none;
      }
    }
  }
  .taskPop{
    .van-popup__close-icon{
      font-size: 15px;
      z-index: 11;
    }
    .taskList{
      .content{
        padding:50px 15px 20px;
        background: #F2F2F2;
        .item{
          display: flex;
          align-items: center;
          justify-content: space-between;
          padding:12px 20px;
          background: #FFFFFF;
          box-shadow: 0px 0px 18px 0px rgba(0,0,0,0.07);
          border-radius: 6px;
          margin-bottom:10px;
          &:nth-last-of-type(1){
            margin-bottom:0px;
          }
          .left{
            max-width: calc(100% - 100px);
            p{
              font-family: PingFangSC-Semibold;
              font-size: 14px;
              color: #1B1B1B;
              letter-spacing: 0.35px;
              font-weight: 600;
              line-height: 20px;
              &:nth-last-of-type(1){
                font-family: PingFangSC-Regular;
                font-size: 12px;
                color: #666666;
                letter-spacing: 0.3px;
                font-weight: 400;
                line-height: 17px;
                margin-top:2px;
              }
            }
          }
          .van-button{
            width: 80px;
            height: 28px;
            background-image: linear-gradient(179deg, #FFD75F 0%, #FFA11C 99%);
            border-radius: 235px;
            font-size: 12px;
            color: #FFFFFF;
            letter-spacing: 0;
            text-align: center;
            font-weight: 600;
            &.dis{
              color:#999;
              background:none;
            }
          }
        }
      }
    }
  }
  .wxTipPop {
    background-color: rgba(0, 0, 0, 0);
    .weixin-share-tip {
      width: 156px;
      margin: 10px 60px 0 0;
      float: right;
    }
  }
  @media screen and (min-width: 750px) {
    .OpenBlindBox .openBlindCont .BlindContWidth{
      width:60%;
    }
    .blindboxPopClass{
      width:450px;
      .giveNum{
        .animation{
          height:375px;
        }
        .animationBg{
          transform: scale(1);
        }
      } 
    }
  }
@keyframes rotate{
    0% {
        transform:rotate(0deg);
        /*
        transform:rotateX(0deg);
        transform:rotate(0deg);
        transform:rotate(0deg);
        */
    }
    100% {
        transform:rotate(360deg);
        /*
        transform:rotateX(360deg);
        transform:rotate(360deg);
        transform:rotate(-360deg);
        */
    }
}
  @-moz-keyframes LR{
    0% {
      -moz-transform: translateX(-3px);
    }
    50% {
      -moz-transform: translateX(3px);
    }
    100% {
      -moz-transform: translateX(-3px);
    }
  }
  @-webkit-keyframes LR{
    0% {
      -webkit-transform: translateX(-3px);
    }
    50% {
      -webkit-transform: translateX(3px);
    }
    100% {
      -webkit-transform: translateX(-3px);
    }
  }
  @keyframes LR{
    0% {
      transform: translateX(-3px);
    }
    50% {
      transform: translateX(3px);
    }
    100% {
      transform: translateX(-3px);
    }
  }

</style>


