<template>
  <section class="head">
    <van-nav-bar title="开盲盒换大奖" v-if="isShowNav" @click-left="onClickLeft">
      <template #left>
        <img
          class="title-back"
          src="//x.autoimg.cn/2sc/2020818/btn_back_white.png"
        />
      </template>
    </van-nav-bar>
    <OpenBlindBox :ispc="this.$attrs.ispc" :attr="this.$attrs" />
    <rightMenu class="rightMenu" :style="{top:isShowNav?'46px':'0'}" :isLogin="this.$attrs.isLogin" :ispc="this.$attrs.ispc" :cityId="this.$attrs.cityId" />
  </section>
</template>
<script>
import { API } from "../../../api/BlindBox";
import { isEscApp, isManApp, isWechat } from "../../../util/util";
import OpenBlindBox from "./OpenBlindBox.vue"
import rightMenu from "./RightMenu.vue"
export default {
  name: "Head",
  props: {
    // cityId: {
    //   type: Number,
    //   default: 0,
    // },
    // ispc: Boolean,
  },
  components:{
    OpenBlindBox,
    rightMenu
  },
  data() {
    return {
      isShowNav: false,
    };
  },
  mounted(){
    this.isInApp();
  },
  methods: {
    //是否在MPC里
    isInApp() {
      this.isShowNav = !(isEscApp() || isManApp() || isWechat());
    },
    //返回
    onClickLeft() {
      history.go(-1);
    },
  },
};
</script>

<style lang="scss" scoped>
.head{
  padding-bottom:16px;
  position: relative;
  overflow:hidden;
  .title-back {
    width: 22px;
    height: 22px;
  }
  .rightMenu{
    position:absolute;
    right:0;
  }
}
</style>


