/**
 * 通用分享参数说明
 * @param {string} title 分享title
 * @param {string} pageTitle 页面title
 * @param {string} content 分享内容
 * @param {string} imageUrl 分享图片url
 * @param {string} url 分享链接url
 * @param {string} target 分享操作触发按钮id
 * @param {string} platform 渠道标识
 * @param {string} callback 初始化成功后的回调函数
 * @param {string} autojsxcx 是否打开小程序,二手车分享好友到小程序卡片
 * @param {string} userName //小程序用户姓名
 * @param {number} type //要跳转的小程序对应的版本，选填 0：正式版本（默认） 1：测试版本 2：体验版本
 * @param {string} path //小程序path
 * @param {boolean} isLogin 是否登录
 * @param {boolean} ispc 是否是PC端，区分pc和m
 * @param  error 分享回调参数 {"1":"是pc","0","没登陆"}
   @param  success 分享回调参数 {"1":"小程序中","2":"主软APP和二手车APP","3":"M或者PC"}
 */

import {
  isEscApp,
  isManApp,
  domAppendJs
} from "../util";
let Mixin_Share = {
  data: () => {
    return {
      mixin_share_isInWeiXinLite: false, //微信小程序中
      mixin_share_isInWeiXin: false, //微信中
      mixin_share_params: {
        ispc: false, //默认是否为pc
        isLogin: false //默认为false 
      },
    }
  },
  mounted() {
    this.mixin_share_isMiniApp();
  },
  methods: {
    //引入wxjssdk
    mixin_share_isMiniApp() {
      domAppendJs("//res.wx.qq.com/open/js/jweixin-1.3.2.js", () => {
        this.mixin_share_isWeChatMiniApp();
      });
    },
    //是否小程序
    mixin_share_isWeChatMiniApp() {
      const ua = window.navigator.userAgent.toLowerCase();
      return new Promise((resolve) => {
        //非微信
        if (ua.indexOf("micromessenger") == -1) {
          this.mixin_share_isInWeiXinLite = false;
        } else {
          wx.miniProgram.getEnv((res) => {
            //小程序中
            if (res.miniprogram) {
              this.mixin_share_isInWeiXinLite = true;
            } else {
              //在微信中
              this.mixin_share_isInWeiXin = true;
              this.mixin_share_isInWeiXinLite = false;
            }
          });
        }
      });
    },
    //初始化分享相关方法
    mixin_share_initShare: function () {
      if (window.fzshare || window.timer) {
        //这样改的原因是：跳转到规则页，再跳回来页面会从新刷新但window.fzshare 绑定的就不是原来的按钮了，导致调起原生的时候会失效
        window.fzshare = null;
        window.timer = 0;
      }
      window.timer = setTimeout(function () {
        window.fzshare = new window.fz.share(this.mixin_share_params.options);
      }, 2000);
    },
    // 分享按钮点击
    mixin_share_shareBtnClick() {
      return new Promise((res, rej) => {
        if (this.mixin_share_params.ispc) {
          rej({
            stauts: "error",
            code: "1"
          });
          return;
        }
        if (this.mixin_share_params.isLogin) {
          if (this.mixin_share_isInWeiXinLite || this.mixin_share_isInWeiXin) {
            res({
              stauts: "success",
              code: "1"
            })
          } else {
            //app端分享
            if (isEscApp() || isManApp()) {
              window.fzshare.clickShare(); //看看有无callback
              res({
                stauts: "success",
                code: "2"
              })
            } else {
              res({
                stauts: "success",
                code: "3"
              })
            }
          }
        } else {
          rej({
            stauts: "error",
            code: "0"
          });
        }
      })
    }
  }
}

export {
  Mixin_Share
}



